import React, { Fragment } from "react";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import { Link } from "react-router-dom";
import PageComponent from "@Components/core/PageComponent";
import style from '../styles.css';
import MainContentService from './../mainContentService/mainContentService';
import LojaVirtualSVG from "./LojaVirtualSVG";

export default class LojaVirtual extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside ecommerce">
            
              <NavBar />
            

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>Loja Virtual</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <Link to="/design">Design</Link>
                  </li>
                  <li>
                    <span>Loja Virtual</span>
                  </li>
                </Breadcrumb>

                <MainContentService
                  service="lojavirtual"
                  svg={LojaVirtualSVG}
                  mainTitle="Mais que apenas sites"
                  subTitle="Crie seu e-commerce aqui!"
                  firstText="Uma loja virtual bem-sucedida deve buscar sempre a melhor experiência possível de compra para o cliente. Não importa o setor ou ramo de atuação, valor do produto, tipo de entrega, o mais importante é que o e-commerce conduza o consumidor de forma objetiva, fácil e atraente até a compra e entrega do produto, respeitando padrões universais de usabilidade e processo de compras ágil. É fundamental ter foco nas necessidades e anseios do consumidor, para que ele se surpreenda positivamente com a conveniência da compra e se torne um cliente recorrente e fidelizado à marca."
                  secondText="O mercado de varejo online vem crescendo ano a ano e é cada vez mais competitivo e popularizado, por isso é necessário investir para se destacar da concorrência. Os hábitos de consumo mudaram e seu negócio precisa estar adaptado a esta realidade."
                />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



