import React, { Fragment } from "react";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import { Link } from "react-router-dom";
import PageComponent from "@Components/core/PageComponent";
import UXSVG from "./UXSVG";
import style from "../styles.css";
import MainContentService from "../mainContentService/mainContentService";

export default class UX extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside usabilidade">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>UX</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <Link to="/design">Design</Link>
                  </li>
                  <li>
                    <span>UX</span>
                  </li>
                </Breadcrumb>

                <MainContentService
                  service="ux"
                  svg={UXSVG}
                  mainTitle="A experiência do usuário"
                  subTitle="Conjunto de aspectos referentes à interação do usuário"
                  firstText="A grande maioria das pessoas tende a desistir rapidamente se percebem que um site não é bem como elas esperavam. Estamos acostumados com a agilidade e facilidade, e cada vez menos temos paciência para sites que não funcionam bem. Daí toda a importância dessa ciência moderna chamada User Experience. UX está relacionada ao design, que é responsável por desenvolver a interface de forma que favoreça a usabilidade e máxima facilidade de entendimento do usuário enquanto navega."
                  secondText="O algoritmo do Google está sempre evoluindo e já consegue reconhecer muitos padrões de UX, valorizando quem os segue e penalizando aqueles que não investem em usabilidade. O resultado disso impacta diretamente no SEO. A dígitozero conta com um time de design com bastante experiência em projetos de usabilidade e antenado nas melhores práticas adotadas pelo mercado atualmente."
                />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



