import React, { Fragment } from "react";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import { Link } from "react-router-dom";
import PageComponent from "@Components/core/PageComponent";
import style from '../styles.css';
import MainContentService from './../mainContentService/mainContentService';
import AppSVG from "./AppSVG";

export default class App extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside site">
            
              <NavBar />
            

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>App</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <Link to="/design">Design</Link>
                  </li>
                  <li>
                    <span>App</span>
                  </li>
                </Breadcrumb>

                <MainContentService
                  service="app"
                  svg={AppSVG}
                  mainTitle="Todas as plataformas e tipos de dispositivos"
                  subTitle="Faça um app para sua marca e fidelize seus clientes"
                  firstText="Lorem ipsum dolor sit amet, consectetur adipiscing elit. Pellentesque aliquam sodales sem eget ornare. Nam lobortis neque quis neque pellentesque egestas. Donec malesuada nibh quis nibh mattis, non tincidunt sapien maximus. Pellentesque pulvinar ac diam vitae lobortis. Suspendisse scelerisque lorem vel nisi consequat, placerat sodales mauris porttitor. In eget nisl id ante pulvinar ultrices. Phasellus lobortis magna vel nisi tempus tempor."
                  secondText="Lorem ipsum dolor sit amet, consectetur adipiscing elit. Pellentesque aliquam sodales sem eget ornare. Nam lobortis neque quis neque pellentesque egestas. Donec malesuada nibh quis nibh mattis, non tincidunt sapien maximus. Pellentesque pulvinar ac diam vitae lobortis. Suspendisse scelerisque lorem vel nisi consequat, placerat sodales mauris porttitor. In eget nisl id ante pulvinar ultrices. Phasellus lobortis magna vel nisi tempus tempor."
                />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



