import React, { Component, Fragment } from "react";
import style from "./styles.css";
import FormOrcamentoServico from "@Components/forms/common/FormOrcamentoServico";

export default class MainContentService extends Component {
  
  getText = () => {
    const { firstText, secondText } = this.props;
    if (secondText)
    {
      return (
        <div className="row">
          <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
            <p style={style.p}>{firstText}</p>
          </div>
          <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">
            <p style={style.p}>{secondText}</p>
          </div>
        </div>
      );
    }
    else
    {
      return (
        <p style={{ ...style.p, textAlign: "center" }}>{firstText}</p>
      )
    }
  };

  render() {
    const { mainTitle, subTitle, service } = this.props;
    return (
      <Fragment>
        <div style={style.background}>
          <div class="container" style={style.subContainer}>
            <this.props.svg />
            <h2 style={style.mainTitle}>{mainTitle}</h2>
            <p style={style.subTitle}>{subTitle}</p>
            <span style={style.hr} />
            {this.getText()}
          </div>
        </div>
        <FormOrcamentoServico service={service} />
      </Fragment>
    );
  }
}



