import React, { Fragment } from "react";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import { Link } from "react-router-dom";
import PageComponent from "@Components/core/PageComponent";
import style from "../styles.css";
import MainContentService from "../mainContentService/mainContentService";
import SiteSVG from "./SiteSVG";

export default class Site extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside site">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>Criação de Sites</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <Link to="/design">Design</Link>
                  </li>
                  <li>
                    <span>Site</span>
                  </li>
                </Breadcrumb>

                <MainContentService
                  service="site"
                  svg={SiteSVG}
                  mainTitle="Mais que apenas sites"
                  subTitle="Websites otimizados para SEO"
                  firstText="Na dígitozero desenvolvemos websites modernos e customizados para todos os segmentos e portes de negócio. Nosso time tem a expertise necessária para desenvolver um site totalmente responsivo para mobile, tablet e também alinhados às melhores práticas do Google: o chamado SEO (Search Engine Optimization). Através de um bom trabalho de SEO, seu um site estará otimizado, assim sua empresa tende a ser posicionada entre as mais bem ranqueadas na primeira página de resultados de pesquisas do Google, atraindo a atenção e disputando cada clique do seu público-alvo com a concorrência."
                  secondText="Entregamos páginas intuitivas, interativas e diretas, focadas na clareza e boa experiência do usuário, o chamado User Experience, resumido na sigla UX. Cada vez mais, o Google valoriza sites que não confundam o usuário, e sim forneçam facilidade, sem exigir que ele tenha que pensar muito. O buscador penaliza páginas que não estejam adaptadas às boas práticas de UX e dificultem a vida de quem navega, por isso é muito importante investir em um bom website para sua empresa. Afinal de contas, é a “cara” do seu negócio."
                />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



