import React, { Fragment } from "react";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import { Link } from "react-router-dom";
import PageComponent from "@Components/core/PageComponent";
import BrandingSVG from "./BrandingSVG";
import MainContentService from "../mainContentService/mainContentService";
import style from '../styles.css';


export default class Branding extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside branding">
            <NavBar />
            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>Branding</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <Link to="/design">Design</Link>
                  </li>
                  <li>
                    <span>Branding</span>
                  </li>
                </Breadcrumb>
                <MainContentService
                  service="branding"
                  svg={BrandingSVG}
                  mainTitle="Muito mais que uma logo"
                  subTitle="Pode ser traduzido de maneira geral como Gestão de Marca"
                  firstText="Pode ser considerado como atividade de Branding qualquer
                  atividade realizada com o objetivo de tornar sua marca mais
                  conhecida, reconhecida (brand awareness ou consciência de
                  marca), desejada e positiva, tanto no pensamento quanto no
                  coração do seu público-alvo. A Gestão de Marca abrange desde a
                  concepção de uma logomarca e identidade visual, até as
                  atividades mais simples de marketing do cotidiano de uma
                  empresa, como um post no Instagram, por exemplo."
                  secondText='A importância de uma boa gestão de marca está na resposta da
                  seguinte pergunta: "Porque você gosta mais de uma marca em
                  detrimento de outra, mesmo sabendo que as duas oferecem um
                  produto ou serviço bastante similar e que resolve seu problema
                  da mesma forma?" O cérebro humano é bastante complexo e
                  abrange fatores racionais e irracionais, por isso é importante
                  ser mais do que apenas uma empresa que vende algo, é preciso
                  vender uma ideia.'
                />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



