import React, { Fragment } from "react";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import { Link } from "react-router-dom";
import PageComponent from "@Components/core/PageComponent";
import MainContentService from '../mainContentService/mainContentService';
import style from "../styles.css";
import DesignSVG from './DesignSVG';

export default class Design extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside design">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>Design</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <span>Design</span>
                  </li>
                </Breadcrumb>

                <MainContentService
                  service="design"
                  svg={DesignSVG}
                  mainTitle="Design"
                  subTitle="Lorem Ipsum Lorem Ipsum Lorem Ipsum Lorem Ipsum Lorem Ipsum Lorem Ipsum"
                  firstText="A equipe de Design da dígitozero é multidisciplinar,
                  criativa e está sempre antenada a toda e qualquer
                  tendência do mercado. Realizamos projetos de branding
                  de diversos tipos, seja qual for a necessidade. Crie
                  uma logomarca marcante para a sua empresa Planejamos a
                  identidade visual. Fazemos sites de acordo com as
                  melhores práticas de UX, Garantimos a melhor
                  usabilidade possível para quem navega"
                />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



