import React, { Fragment } from "react";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import { Link } from "react-router-dom";
import PageComponent from "@Components/core/PageComponent";
import style from '../styles.css';
import LojaVirtualSVG from './../Development/LojaVirtualSVG';
import MainContentService from '../mainContentService/mainContentService';
import InboundSVG from "./InboundSVG";

export default class Inbound extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configurção landscape --> */}
        <div id="lying" />

        {/* <!-- Configurção natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside blog-corporativo">
            
              <NavBar />
            

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>Inbound Marketing</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dígitozero</Link>
                  </li>
                  <li>
                    <span>Inbound</span>
                  </li>
                </Breadcrumb>

                <MainContentService
                  service="inboundmarketing"
                  svg={InboundSVG}
                  mainTitle="Atraia seu consumidor"
                  subTitle="Seja estrategista"
                  firstText="Inbound Marketing também conhecido como Marketing de Atração, é a estratégia fundamental do Marketing Digital. O inbound tem o objetivo principal de atrair o consumidor e possui três pilares fundamentais: Criação de Conteúdo, ou Marketing de Conteúdo; Gestão de Redes Sociais e SEO. Aqui na dígitozero oferecemos todos os serviços relacionados ao Inbound Marketing."
                  secondText="Criamos conteúdo em diversos formatos e mídias, para redes sociais e sites. Fazemos auditoria de SEO & Consultoria de SEO Criamos sites otimizados e otimizamos sites já existentes. Fazemos uma gestão estratégica das redes sociais, focada em engajamento e conversão."
                />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



